import React from "react";
import success from "../images/Union.svg";
import fail from "../images/Union(1).svg";
import cross from "../images/Vector(1).svg";

class InfoToolTip extends React.Component {
  render() {
    return (
      <>
        <div className={`popup ${this.props.isOpen ? "popup_opened" : ""}`}>
          <div className="popup__container">
            <button
              className="popup__close-button"
              type="button"
              onClick={this.props.onClose}
            >
              <img src={cross} alt="–∑–∞–∫—Ä—ã—Ç—å" className="popup__close-image" />
            </button>
            <img
              alt={this.props.success ? "—É—Å–ø–µ—Ö" : "–Ω–µ—É–¥–∞—á–∞"}
              src={this.props.success ? success : fail}
              className="popup__union"
            />
            <p className="popup__message">
              {this.props.success
                ? "–í—ã —É—Å–ø–µ—à–Ω–æ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–ª–∏—Å—å!"
                : "–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑."}
            </p>
          </div>
        </div>
      </>
    );
  }
}
export default InfoToolTip;
