class Api {
  // –ø–æ—Å–ª–µ –ø–µ—Ä–µ–∏–º–µ–Ω–æ–≤–∞–Ω–∏—è –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∞—è –ø—Ä–æ–≤–µ—Ä–∫–∞ –ø—Ä–æ–µ–∫—Ç–∞ –ø–µ—Ä–µ–¥ —Ä–µ–≤—å—é –ø–∏—à–µ—Ç: –ù–µ —ç–∫—Å–ø–æ—Ä—Ç–∏—Ä–æ–≤–∞–Ω –∫–ª–∞—Å—Å Api.
  constructor(options) {
    this._baseUrl = options.baseUrl;
    // this.likeBind = this.like.bind(this);
    // this.unLikeBind = this.unLike.bind(this);
    this.changeLikeCardStatus = this.changeLikeCardStatus.bind(this);
    this.deleteCardBind = this.deleteCard.bind(this);
  }

  getInitialCards() {
    return fetch(`${this._baseUrl}/cards`, {}).then((res) => this._check(res));
  }

  getUserInfo() {
    return fetch(`${this._baseUrl}/users/me`, {}).then(this._check);
  }

  patchUserInfo(name, about) {
    return fetch(`${this._baseUrl}/users/me`, {
      method: "PATCH",
      body: JSON.stringify({
        name: name,
        about: about,
      }),
    }).then((res) => this._check(res));
  }

  editAvatar(link) {
    return fetch(`${this._baseUrl}/users/me/avatar`, {
      method: "PATCH",
      body: JSON.stringify({
        avatar: link,
      }),
    }).then((res) => this._check(res));
  }

  addCard(name, link) {
    return fetch(`${this._baseUrl}/cards`, {
      method: "POST",
      body: JSON.stringify({
        name: name,
        link: link,
      }),
    }).then((res) => this._check(res));
  }

  // like(id) {
  //   return fetch(`${this._baseUrl}/cards/likes/${id}`, {
  //     method: "PUT",
  //   }).then((res) => this._check(res));
  // }

  changeLikeCardStatus(id, isLike) {
    if (isLike) {
      return fetch(`${this._baseUrl}/cards/likes/${id}`, {
        method: "PUT",
      }).then((res) => this._check(res));
    } else {
      return fetch(`${this._baseUrl}/cards/likes/${id}`, {
        method: "DELETE",
      }).then((res) => this._check(res));
    }
  }

  // unLike(id) {
  //   return fetch(`${this._baseUrl}/cards/likes/${id}`, {
  //     method: "DELETE",
  //   }).then((res) => this._check(res));
  // }

  deleteCard(id) {
    return fetch(`${this._baseUrl}/cards/${id}`, {
      method: "DELETE",
    }).then((res) => this._check(res));
  }

  _check(res) {
    if (res.ok) {
      return res.json();
    } else {
      console.log(`–û—à–∏–±–∫–∞: ${res.status}`);
      return Promise.reject(`–û—à–∏–±–∫–∞: ${res.status}`);
    }
  }
}
const api = new Api({
  baseUrl: "http://api.timofei.pustarnak.nomoredomains.icu",
});
export default api;
